'use server';

import db from '../lib/db';
import { auth } from '../auth';

export async function getAdminStats() {
  const session = await auth();
  if (session?.user?.role !== 'ADMIN') {
    throw new Error('Unauthorized');
  }

  try {
    const [totalMembers, totalBusinesses, totalDonations] = await Promise.all([
      db.user.count(),
      db.business.count(),
      db.donation.aggregate({ _sum: { amount: true } }),
    ]);

    return {
      totalMembers,
      pendingApprovals: 0, // Placeholder - would need a status field in User model
      totalBusinesses,
      totalDues: totalDonations._sum.amount || 0,
    };
  } catch (error) {
    console.error('Failed to fetch admin stats:', error);
    throw new Error('Failed to fetch admin stats.');
  }
}
